import React, { memo } from "react";
import FormErrors from "../../common/components/FormErrors";
import FormStyleProvider from "./FormStyleProvider";

const Form1 = ({ register, errors, isLoading }) => {
  return (
    <FormStyleProvider>
      <div className="step__form--general page--1 w-100 d-flex flex-column flex-lg-row">
        <div className="step__form--general--left">
          <p className="fs__title--main fw-bolder">Nhập thông tin</p>
          <div className="step__form__input">
            <input
              type="text"
              name="fullname"
              placeholder="Họ và tên đầy đủ"
              ref={register({ required: "Cần nhập đầy đủ thông tin" })}
              disabled={isLoading}
            />
            <input
              type="date"
              name="birth"
              ref={register({ required: "Cần nhập đầy đủ thông tin" })}
              disabled={isLoading}
            />
          </div>
          <FormErrors errors={errors} />
        </div>
        <div className="step__form--general--right">
          <p className="fs__title--white">Xin chào</p>
          <p className="fs__text">
            Để nhận được bài báo cáo số học 17 chỉ số của bạn hãy điền họ tên
            đầy đủ và ngày tháng năm sinh
          </p>
          <p className="fs__details">
            Lưu ý: <br />- Cần sử dụng ngày tháng năm sinh trên giấy tờ khai
            sinh của bạn (không sử dụng ngày âm lịch) <br />
            - Vui lòng điền đúng tên của bạn (in hoa và đầy đủ dấu) <br />
            <div
              className="button-secondary--outline p-3"
              style={{
                width: "fit-content",
                background: "white",
                color: "black",
              }}
            >
              Chat với hỗ trợ viên
            </div>
          </p>
        </div>
      </div>
    </FormStyleProvider>
  );
};

export default memo(Form1);
